import React from 'react';
import { Group, Cell, Header, Counter, List, Alert } from '@vkontakte/vkui';
import { Icon20DonateCircleFillYellow, Icon20MoneyCircleOutline } from '@vkontakte/icons';
import { useRouter, usePopout } from '@unexp/router'



const Status = ({ properties }) => {
    let { push } = useRouter();
	let { setPopout, closePopout } = usePopout();
    return (
        <Group info="Information for user" mode="card">
            <List> <Header>Ваше состояние</Header>
                <Cell onClick={() => push({ view: 'info', panel: 'info' })} before={<Icon20DonateCircleFillYellow />} indicator={<Counter mode="primary">{properties.balance}</Counter>}>Баллы</Cell>
                <Cell onClick={async () => 
						setPopout(
						<Alert
							actions={[
								{ title: 'Хорошо..', autoclose: true, mode: 'cancel' },
							]}
							actionsLayout='vertical'
							onClose={closePopout}
							header='В разработке'
							text='Пополение личного счёта пока в разработке'
						/>
					)}
                    before={<Icon20MoneyCircleOutline />} indicator={<Counter mode="primary">{properties.rub}</Counter>}>Рубли</Cell>
            </List>
        </Group>
    )
}

export default Status;
